<template>
  <div class="series-catalogue">
    <section class="head-detail clearfix">
      <div class="image" style="min-width: 360px;">
        <img :src="mtsCatalog.cover" alt="封面" />
      </div>
      <div class="info">
        <h4 class="info-title">{{ mtsCatalog.title }}</h4>
        <p class="sign">课程类型：{{ getType(mtsCatalog.type) }}</p>
        <p class="sign">课程节数：{{ courseList.length }}节</p>
        <p class="sign flex-wrap clearfix">
          <span style="min-width: 70px;">章节导言：</span>
          <span>{{ mtsCatalog.summary }}</span>
        </p>
      </div>
    </section>
    <div class="list-detail">
      <h4 class="title">课程目录</h4>
      <div class="list">
        <div v-for="(item, index) of courseList" :key="index" class="item">
          <p class="title-info">
            <img
              v-if="item.type === 'video'"
              src="https://pic1.baobaohehu.com/fd/admin_v3/5d7f5f5c1c03121b82179aee//20200217180816740/1581934096405.png"
              alt="视频"
            />
            <img
              v-if="item.type === 'audio'"
              src="https://pic1.baobaohehu.com/fd/admin_v3/5d7f5f5c1c03121b82179aee//20200217180902469/1581934142175.png"
              alt="音频"
            />
            <img
              v-if="item.type === 'image_text'"
              src="https://pic1.baobaohehu.com/fd/admin_v3/5d7f5f5c1c03121b82179aee//20200217180936086/1581934175789.png"
              alt="图文"
            />
            <router-link :to="`/study/course-detail/${item._id}`"
              ><span>{{ index + 1 }}.{{ item.title }}</span></router-link
            >
          </p>
          <p class="time">
            {{ item.created_at.split('T')[0] }}
            {{ item.created_at.split('T')[1].split('.')[0] }}
          </p>
        </div>
      </div>
      <div class="pagination">
        <el-pagination
          :current-page="currentPage"
          :page-sizes="[12, 18, 24]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="count"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { getCourseList } from '@/api/learningAdmin'
export default {
  props: {
    catalog: {
      type: Object,
      default: () => {
        return {}
      }
    },
    list: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
      typeList: [],
      course: {
        type: '',
        name: ''
      },
      courseList: [],
      currentPage: 1,
      delShow: false,
      addCourseShow: false,
      pageSize: 4,
      count: 0,
      rowInfo: '',
      inputDel: '',
      disabledDel: true,
      delType: 'info',
      mtsCatalog: {}
    }
  },
  computed: {
    getType() {
      return function(type) {
        let str = ''
        if (type) {
          if (type.indexOf('image_text') !== -1) str += ' 图文'
          if (type.indexOf('audio') !== -1) str += ' 音频'
          if (type.indexOf('video') !== -1) str += ' 视频'
        }
        return str
      }
    }
  },
  watch: {
    catalog() {
      this.mtsCatalog = this.catalog
      this.courseList = this.list
    }
  },
  created() {
    this.courseList = this.list
    this.mtsCatalog = this.catalog
    if (this.$route.params.id) this.getCourseDetailList()
  },
  methods: {
    handleSizeChange(val) {
      this.pageSize = val
      this.getCourseDetailList()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getCourseDetailList()
    },
    setDelStatus() {
      if (this.inputDel === '删除') {
        this.disabledDel = false
        this.delType = 'primary'
      } else {
        this.disabledDel = true
        this.delType = 'info'
      }
    },
    selectCourse() {
      this.addCourseShow = true
    },
    confirmCourse() {},
    async getCourseDetailList() {
      const { data, code } = await getCourseList({
        id: this.$route.params.id,
        page: this.currentPage,
        pageSize: this.pageSize
      })
      if (code === 20000 && data.result) {
        this.mtsCatalog = data.mtsCatalog
        this.courseList = data.list
        this.count = data.count
      }
    }
  }
}
</script>

<style scoped lang="scss">
.series-catalogue {
  .head-detail {
    display: flex;
    padding: 20px;
    height: 240px;
    border: 1px solid #ebeef5;
    border-radius: 4px;
    flex: 1;
    .image {
      float: left;
      width: 360px;
      height: 200px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .info {
      float: left;
      padding: 14px 20px;
      .info-title {
        font-size: 16px;
        color: #17233d;
        margin-bottom: 20px;
      }
      .sign {
        font-size: 14px;
        color: #999;
        margin-bottom: 10px;
        &.flex-wrap {
          display: flex;
          flex: 1;
          span:last-child {
            display: -webkit-box;
            overflow: hidden;
            text-overflow: ellipsis;
            -webkit-line-clamp: 5;
            -webkit-box-orient: vertical;
          }
        }
      }
    }
  }
  .list-detail {
    margin-top: 20px;
    padding: 20px;
    border: 1px solid #e8eaec;
    border-radius: 4px;
    .list {
      .item {
        p {
          margin: 10px 0 14px;
        }
        p.title-info {
          img {
            margin-right: 10px;
            width: 12px;
            height: 12px;
          }
          span {
            font-size: 14px;
            color: #576277;
          }
        }
        p.time {
          font-size: 12px;
          color: #999;
        }
      }
    }
  }
  .pagination {
    margin-top: 50px;
    text-align: center;
  }
  .add-course {
    float: right;
    line-height: 40px;
  }
  /deep/.del-input {
    margin-top: 15px;
    input {
      width: 220px;
    }
  }
  .set-status {
    /deep/.el-dialog__body {
      padding: 0 25px 10px;
    }
  }
  .title {
    padding-bottom: 20px;
    font-size: 20px;
    font-weight: bold;
    color: #333;
  }
}
</style>
